<template>
<v-card flat :color="color" dark elevation="1">
    <v-card-title class="pb-0" v-if="!loading">
        <v-icon dark class="px-2" v-if="icon">{{ icon }}</v-icon>
        {{ title }} 
        <v-spacer></v-spacer>
        <v-btn icon dark v-if="appendIcon" @click="$emit('click:icon')"><v-icon>{{appendIcon}}</v-icon></v-btn>
    </v-card-title>
    <v-card-title class="pb-0" v-else><v-skeleton-loader
                type="heading"
                ></v-skeleton-loader></v-card-title>
    
    <v-card-text>
        
        <v-row v-if="!loading">
            <v-col class=" white--text" v-if="deposit" >
                <small>Deposits</small><h1>{{ count }}</h1>
                
            </v-col>
            <v-col class=" white--text"><small>Total</small><h1>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: currency,  }).format(amount) }}</h1></v-col>
            
        </v-row>
        <v-row v-else>
            <v-col class=" white--text" v-if="deposit" >
               <v-skeleton-loader
                type="heading"
                ></v-skeleton-loader>
            </v-col>
            <v-col class=" white--text">
                <v-skeleton-loader
                type="heading"
                ></v-skeleton-loader>
            </v-col>
        </v-row>
    </v-card-text>
</v-card>
</template>
<script>
export default {
    name: 'Panel',
    props: {
        title: {type: String, default: 'Total'},
        icon: {type: String, default: 'mdi-'},
        count: {type: Number, default: 0},
        amount:{type: Number, default: 0},
        color: {type: String, default: 'green'},
        deposit: { type: Boolean, default: true},
        loading: { type: Boolean, default: false },
        currency: {type: String, default: 'USD'},
        appendIcon: { type: String }
    }
}
</script>